.book-panel {
  .cover-container {
    margin: 0 auto;
  }

  .cover {
    width: 22rem;
    box-sizing: border-box;
    box-shadow: 10px 5px 10px black;
  }

  .pull-quote-and-summary-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    margin-left: 3rem;
    margin-right: 2rem;

    line-height: normal;
  }

  .select-format-button {
    cursor: pointer;
    text-align: center;

    margin: 1rem auto 0;
    border-radius: 0.25rem;
    padding: 1rem;
    width: 10rem;

    color: white;
    &.physical { background-color: var(--color-physical-green); }
    &.kindle { background-color: var(--color-kindle-blue); }
    &.audible { background-color: var(--color-audible-orange); }

    &.selected {
      // Alternative to border that doesn't affect layout at all.
      box-shadow: inset 0 0 0 0.25rem black;
    }

    &.unavailable {
      cursor: initial;
      opacity: 0.25;
    }
    
    &:hover:not(.unavailable) {
      background-color: white;
      color: black;
    }
  }

  @media(max-width: 800px) {
    /* When on narrow screen... */

    /* Stack the top quote and the cover image on top of each other rather than side-to-side. */
    .cover-and-quote-container {
      flex-wrap: wrap;
    }
    .pull-quote-and-summary-container {
      margin-top: 3rem;
      margin-left: 0;
      margin-right: 0;
    }
    .summary-container {
      margin-top: 3rem;
    }

    .cover {
      /* Make the image of the cover smaller. */
      // If you change this, also see SelectABook.scss.
      width: 18rem;      
    }
  }
}